<template>
  <ul class="lv1">
    <li
      v-for="(item, i) in menu"
      :key="item.title"
      class="h3-style"
      :class="{ active: activeItem == i }"
    >
      <router-link :to="item.path" v-html="item.title"></router-link>

      <ul class="me-0" v-if="item.submenu.length > 0">
        <li v-for="(sub, j) in item.submenu" :key="j" class="submenu">
          <router-link :to="sub.path" v-html="sub.title"></router-link>
        </li>
      </ul>
    </li>
  </ul>
</template>

<script>
export default {
  name: 'SideMenu',
  props: {
    menu: { type: Array, required: true },
  },
  data() {
    return {
      activeItem: -1,
      lastScrollPosition: 0,
    };
  },
  mounted() {
    window.addEventListener('scroll', this.onScroll);
  },
  beforeUnmount() {
    window.removeEventListener('scroll', this.onScroll);
  },
  methods: {
    onScroll() {
      // Get the current scroll position
      const currentScrollPosition = window.pageYOffset || document.documentElement.scrollTop;
      // Because of momentum scrolling on mobiles, we shouldn't continue if it is less than zero
      if (currentScrollPosition < 0) {
        return;
      }
      // Here we determine whether we need to show or hide the navbar
      this.showNavbar = currentScrollPosition < this.lastScrollPosition;
      // Set the current scroll position as the last scroll position
      this.lastScrollPosition = currentScrollPosition;
    },
  },
};
</script>

