import { createRouter, createWebHistory } from 'vue-router';
import Home from '../views/Home.vue';
import NormalPage from '../views/NormalPage.vue';
import About from '../views/About.vue';
import CampList from '../views/CampList.vue';
import CampList3 from '../views/CampList3.vue';
import CampList4 from '../views/CampList4.vue';
import CampCourses from '../views/CampCourses.vue';
import Download from '../views/Download.vue';
import Records from '../views/Records.vue';
import OnlineCourses from '../views/OnlineCourses.vue';
import Apply from '../views/Apply.vue';
import NotFound from '../views/NotFound.vue';

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
  }, {
    path: '/*',
    name: 'Page',
    component: NormalPage,
    children: [
      {
        path: '/about',
        name: 'About',
        component: About,
      }, {
        path: '/camp-lists/phase1',
        name: 'CampList',
        component: CampList,
      }, {
        path: '/camp-lists/phase2',
        name: 'CampList2',
        component: CampList,
      }, {
        path: '/camp-lists/phase3',
        name: 'CampList3',
        component: CampList3,
      }, {
        path: '/camp-lists/phase4',
        name: 'CampList4',
        component: CampList4,
      }, {
        path: '/camp-courses',
        name: 'CampCourses',
        component: CampCourses,
      }, {
        path: '/download',
        name: 'Download',
        component: Download,
      }, {
        path: '/records',
        name: 'Records',
        component: Records,
      },
      {
        path: '/online-courses',
        name: 'OnlineCourses',
        component: OnlineCourses,
      },
      {
        path: '/apply',
        name: 'Apply',
        component: Apply,
      },

    ],
  }, {
    path: '/',
    name: 'Home',
    component: Home,
  },
  // {
  //   path: '/en',
  //   name: 'HomeEN',
  //   component: HomeEN,
  // },
  {
    path: '/:catchAll(.*)',
    name: '404',
    component: NotFound,
  },

  // {
  //   path: '/about',
  //   name: 'About',
  // route level code-splitting
  // this generates a separate chunk (about.[hash].js) for this route
  // which is lazy-loaded when the route is visited.
  //   component: () => import(/* webpackChunkName: "about" */ '../views/About.vue'),
  // },
];

const router = createRouter({
  history: createWebHistory(),
  // base: '/2022MOE/',
  routes,
});

export default router;
