<template>
  <div id="fb-root"></div>
  <!-- main banner -->
  <div
    id="home-banner"
    class="container-fluid d-flex flex-column justify-content-end align-items-center"
    :style="image"
  >
    <div class="call-to-action d-flex align-items-center">
      <div class="old-h3-bold">報名即日起至109年6月3日止</div>
      <router-link
        to="/apply"
        class="group-link align-items-center d-inline-flex tag-link"
        >前往報名<img class="link ms-1" :src="linkArrow" alt="產品設計組"
      /></router-link>
    </div>
    <div id="scroll-hint">
      <img :src="scrollUp" alt="向下捲動" />
    </div>
  </div>
  <div class="home container page">
    <div class="row">
      <div class="col-3 position-relative d-none d-lg-block">
        <div :class="{ fixed: isFixed }">
          <SideMenu :menu="menu[$route.name]"></SideMenu>
        </div>
      </div>
      <div class="col-12 col-lg-9">
        <!-- news carousel -->
        <section id="news">
          <Carousel
            :settings="settings"
            :breakpoints="breakpoints"
            class="h-bar-carousel"
          >
            <Slide v-for="i in 5" :key="i">
              <NewsArticle :news="news[i]" />
            </Slide>
            <template #addons>
              <Navigation />
              <Pagination />
            </template>
          </Carousel>
        </section>
        <!-- FB page post -->
        <section id="fb" class="text-center d-none d-md-block">
          <div
            class="fb-page"
            data-href="https://www.facebook.com/www.moe.edu.tw/"
            data-tabs="timeline"
            data-width="500"
            data-height="800"
            data-small-header="true"
            data-adapt-container-width="false"
            data-hide-cover="true"
            data-show-facepile="false"
          >
            <blockquote
              cite="https://www.facebook.com/www.moe.edu.tw/"
              class="fb-xfbml-parse-ignore"
            >
              <a href="https://www.facebook.com/www.moe.edu.tw/">
                藝術與設計菁英海外培訓計畫
              </a>
            </blockquote>
          </div>
        </section>
        <!-- random participants -->
        <section id="participants" class="d-none d-md-block">
          <div class="row row-grid">
            <div class="col-12 col-md-6" v-for="p in info" :key="p.name">
              <Participant :info="p" />
            </div>
          </div>
        </section>
      </div>
    </div>
  </div>
</template>

<script>
import {
  Carousel, Navigation, Pagination, Slide,
} from 'vue3-carousel';
import 'vue3-carousel/dist/carousel.css';
import menu from '@/assets/js/menu';

import SideMenu from '@/components/global/SideMenu.vue';
import NewsArticle from '@/components/home/NewsArticle.vue';
import Participant from '@/components/home/Participant.vue';
import banner from '@/assets/img/banner.png';
import scrollUp from '@/assets/img/scroll_up.svg';
import linkArrow from '@/assets/img/link_arrow_w.png';

// temp images
import pic1 from '@/assets/img/image_placeholder.png';
import pic2 from '@/assets/img/image_placeholder2.png';
import pic3 from '@/assets/img/image_placeholder3.png';
import pic4 from '@/assets/img/image_placeholder4.png';

export default {
  name: 'Home',
  components: {
    SideMenu,
    NewsArticle,
    Participant,
    Carousel,
    Slide,
    Navigation,
    Pagination,
  },
  data() {
    return {
      scrollUp,
      linkArrow,
      menu,
      // carousel settings
      settings: {
        itemsToShow: 1.08,
        snapAlign: 'start',
      },
      // breakpoints are mobile first
      // any settings not specified will fallback to the carousel settings
      breakpoints: {
        // 700px and up
        // 700: {
        //   itemsToShow: 3.5,
        //   snapAlign: 'center',
        // },
        // 1024 and up
        1024: {
          itemsToShow: 2.2,
          snapAlign: 'start',
        },
      },
      isFixed: false,
      isFullHeader: true,
      image: {
        backgroundImage: `url('${banner}')`,
      },
      news: [],
      info: [
        {
          img: pic1, name: '廖恬敏', title: '獨立字體設計師', tags: ['99年度'],
        },
        {
          img: pic2, name: '李子強', title: '雲科大數媒系 助理教授', tags: ['95年度'],
        },
        {
          img: pic3, name: '陳輝泰', title: 'Lippincott 資深設計師', tags: ['102年度'],
        },
        {
          img: pic4, name: '彭冠傑', title: '博物映項 藝術總監', tags: ['102年度'],
        },
      ],
    };
  },
  created() {
    window.fbAsyncInit = () => {
      window.FB.init({
        appId: '554106528099113',
        cookie: true,
        xfbml: true,
        version: 'v13.0',
      });
      // window.FB.xfbml.parse();
    };
  },
  methods: {
    sideMenuOnScroll() {
      const homeBannerHeight = (document.getElementById('home-banner')) ? document.getElementById('home-banner').offsetHeight : 0;
      const bannerHeight = homeBannerHeight + document.getElementById('main-nav').offsetHeight;
      this.isFixed = bannerHeight < window.scrollY;
    },
  },
  mounted() {
    window.addEventListener('scroll', this.sideMenuOnScroll);
  },
  destroy() {
    window.removeEventListener('scroll', this.sideMenuOnScroll);
  },
  loadFacebookSDK(d, s, id) {
    let js = null;
    const fjs = d.getElementsByTagName(s)[0];
    if (d.getElementById(id)) {
      return;
    }
    js = d.createElement(s);
    js.id = id;
    js.src = 'https://connect.facebook.net/zh_TW/sdk.js';
    fjs.parentNode.insertBefore(js, fjs);
  },
};
</script>

