<script>
import logo from '@/assets/img/logo.png';
import linkArrow from '@/assets/img/link_arrow_w.png';

export default {
  data() {
    return {
      logo,
      linkArrow,
      isFullHeader: false,
      isFixed: false,
    };
  },
  methods: {
    topMenuOnScrollHome() {
      this.isFullHeader = window.scrollY < 10;
    },
    topMenuOnScroll() {
      this.isFixed = window.scrollY > 0;
    },
  },
  watch: {
    $route() {
      // Check if given route is true, if it is then hide Nav.
      if (this.$route.name === 'Home') {
        this.isFullHeader = true;
        this.isFixed = true;
        window.addEventListener('scroll', this.topMenuOnScrollHome);
        window.removeEventListener('scroll', this.topMenuOnScroll);
      } else {
        this.isFullHeader = false;
        this.isFixed = false;
        window.addEventListener('scroll', this.topMenuOnScroll);
        window.removeEventListener('scroll', this.topMenuOnScrollHome);
      }
    },
  },
  destroy() {
    window.removeEventListener('scroll', this.topMenuOnScrollHome);
    window.removeEventListener('scroll', this.topMenuOnScroll);
  },
};
</script>

