<template>
  <Header></Header>
  <GroupTab v-if="showGroupTab"></GroupTab>
  <router-view />
  <Footer></Footer>
</template>

<script>
import Header from '@/components/global/Header.vue';
import GroupTab from '@/components/global/GroupTab.vue';
import Footer from '@/components/global/Footer.vue';

export default {
  name: 'Home',
  components: {
    Header,
    GroupTab,
    Footer,
  },
  data() {
    return {
      groupPages: ['Download', 'About', 'CampList', 'CampList2', 'CampList3', 'CampList4', 'CampCourses'],
    };
  },
  computed: {
    showGroupTab() {
      return (this.groupPages.indexOf(this.$route.name) > -1);
    },
  },
};
</script>

